#accordian 
{
    width:620px;
    position:absolute;
}
#accordian li 
{
    margin: 0px;
    list-style:none;
    width:100%;
    margin-bottom:20px;
    padding: 10px 20px;
    background:#fff;
    display: inline-flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    gap:10px;
    border-radius: 24px;
    background: #FFF;
    box-shadow: 0px 0px 4px 0px rgba(0, 0, 0, 0.25);

}
#accordian li label 
{
    display: inline-flex;
    align-items: center;
    gap: 190px;
    margin-top:10px;
    color: #4D4D4D;
    font-family: Outfit;
    font-size: 32px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
    cursor:pointer;
}
#accordian label + input[type="radio"]
{
    display:none;
}
.dropdown-text p
{
    color: #616161;
    text-align: justify;
    font-family: Outfit;
    font-size: 24px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}
#accordian .dropdown-text
{
    max-height:0;
    overflow:hidden;
    transition:max-height 0.5s;
}
#accordian label + input[type="radio"]:checked + .dropdown-text
{
    max-height:400px;
}
