.navbar{
    position:fixed;
    top:-200px;
    left:6%;
    height:48px;
    width:1280px;
    border-radius:48px;
    display:flex;
    justify-content:center;
    align-items:center;
    gap:340px;
    padding: 14px 24px 14px 36px;
    border: 1px solid rgba(87, 87, 87, 0.32);
    background: rgba(255, 255, 255, 0.23);
    backdrop-filter: blur(39.5px);
    font-family:outfit;
}
.flex
{
    color:black;
}
.active 
{
    color:white;
}
.active-link 
{
    color:white;
}
.flex-link
{
    color:black;
}
.scrolled
{
    color:white;
}
.btn 
{
    color:white;
    border-radius: 48px;
    background: #1F1F1F;
    backdrop-filter: blur(2px);
    padding: 12px 24px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
    cursor:pointer;
}
.navbar-links-container 
{
    display: flex;
    justify-content: center;
    align-items: flex-start;
    gap: 64px;  
    font-family:outfit;
}
.logo-container
{
    height:24px;
}
.logo 
{
    cursor:pointer;
    width:125px;
}